* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: sans-serif;
    list-style: none;
    outline: none !important;
    text-decoration: none !important;
}

body {
    color: white;
    background: #0F111A;
}


.container {

    max-width: 80vw;
    margin: 6rem auto;
}

header{
    background: black;
    box-shadow: 0 1rem 0.5rem rgba(0, 0, 0, 0.5);
    height: 12vh;
}


nav {
    display: flex;
    align-items: center;
    justify-content: space-between;
    height: 12vh;
    max-width: 80vw;
    margin: auto;
}

nav ul li {

    display: inline-block;
    margin-left: 2rem;
}

h1 {
    color:white
}

nav ul li a {
    color: #0F111A;
    padding: 0.4rem ;
    font-weight: 300;
    color: white;
}


#Home{

    display: flex;
    align-items: center;
    justify-content: space-between;
}

.image img{

    height: 250px;
    border-radius: 1rem;
}

.content p{
    padding-top: 1rem;
    font-size: 1.5rem;
    text-transform: capitalize;
   
}


#service h1{
    text-align: center;
    padding-bottom: 1rem;
}

#card_wraper{

    display: grid;
    grid-template-columns: repeat(3 , 1fr);
}

.cards{

    width: 350px;
}


.title h4{

    padding-top: 1rem;
    padding-bottom: 0.5rem;
}

.para p{
    line-height: 1.7rem;
}
.cards .image img{

    width: 100%;
}